import React, { Suspense } from "react"
import BulbLoader from "../components/bulb-loader"

const LandingPage = React.lazy(() => import("./landing")) // Lazy-loaded

const IndexPage = () => {
  return (
    <Suspense fallback={<BulbLoader isLoading={true} />}>
      <LandingPage />
    </Suspense>
  )
}

export default IndexPage
